<template>
  <v-app>
    <v-main>
      <transition
        name="page-fade"
        mode="out-in"
      >
        <router-view 
          :key="$route.fullPath"
          class="page-transition"
        />
      </transition>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'App',

  data: () => ({
    // placeholder for reactive state if needed
  }),

  mounted() {
    this.initVisibilityNotifications();
    // Block right-click context menu globally
    window.addEventListener('contextmenu', this.handleContextMenu);
    // Listen for visibility changes to notify user when page is hidden
    document.addEventListener('visibilitychange', this.handleVisibilityChange);
  },

  beforeUnmount() {
    window.removeEventListener('contextmenu', this.handleContextMenu);
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
    this.stopTitleBlink();
  },

  methods: {
    handleContextMenu(e) {
      // Prevent the default context menu
      e.preventDefault();
    },

    async initVisibilityNotifications() {
      // store original title for blink fallback
      this.originalTitle = document.title;
      this.titleTimer = null;

      // request Notification permission once (if supported)
      if ('Notification' in window) {
        try {
          if (Notification.permission === 'default') {
            await Notification.requestPermission();
          }
        } catch (err) {
          // ignore permission errors
        }
      }
    },

    handleVisibilityChange() {
      if (document.hidden) {
        // When user leaves the tab, show a notification (if allowed)
        this.showHiddenNotification();
        // start title blink fallback so user notices tab
        this.startTitleBlink();
      } else {
        // restore title and stop blink
        this.stopTitleBlink();
        if (this.originalTitle) document.title = this.originalTitle;
        try { window.focus(); } catch (e) { /* ignore */ }
      }
    },

    showHiddenNotification() {
      if (!('Notification' in window)) return;
      if (Notification.permission === 'granted') {
        try {
          const opts = {
            body: 'La tienda sigue abierta. Haz click para volver.',
            icon: '/img/icons/icon-192.png',
            tag: 'store-notif'
          };
          const n = new Notification('üîî Vuelve a Multi Tienda', opts);
          n.onclick = function () {
            try { window.focus(); } catch (e) {}
            this.close?.();
          };
        } catch (err) {
          // fallback silence
        }
      } else {
        // If notifications are not allowed, we rely on title blink only (already started)
      }
    },

    startTitleBlink() {
      if (this.titleTimer) return;
      let visible = false;
      const alt = 'üîî Vuelve a la tienda';
      this.titleTimer = setInterval(() => {
        document.title = visible ? this.originalTitle : alt;
        visible = !visible;
      }, 1000);
    },

    stopTitleBlink() {
      if (this.titleTimer) {
        clearInterval(this.titleTimer);
        this.titleTimer = null;
      }
      if (this.originalTitle) document.title = this.originalTitle;
    },
  },
}
</script>

<style>
